.background-element {
  width: 100vw;
  position: relative;
  margin-bottom: 70px;
}

.header-background {
  position: relative;
}

.header-title-wrapper {
  display: -ms-flexbox;
  display: flex;
  -ms-flex-pack: center;
      justify-content: center;
  bottom: 0vh;
  margin-left: 10vw;
  margin-right: 10vw;
  background: rgba(248, 237, 166, 0.9);
  height: 15vh;
  width: 85%;
  position: absolute;
}

.responsive-text {
  -ms-flex-item-align: center;
      align-self: center;
  -ms-flex-positive: 0.7;
      flex-grow: 0.7;
  width: 75%;
  font-size: 80%;
}
@media (min-width: 600px) {
  .responsive-text {
    font-size: 2vw;
  }
}
@media (max-width: 320px) {
  .header-wrapper {
    width: 320px;
    -ms-flex-item-align: center;
        align-self: center;
  }
}



